.uploader {
    width: 100%;
}

.menuWrapper {
    position: relative;
    display: inline-block;
    width: 100%;
}

.menuButton {
    cursor: pointer;
    padding: 16px 12px;
    border-bottom: 1px solid #000;
    font-weight: 500;
    transition: background-color 0.2s;
}

.menuButton:hover {
    background-color: #ddd;
}

.dropdown {
    cursor: pointer;
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    padding: 10px;
    border-radius: 4px;
    margin-top: 4px;
    z-index: 10;

    opacity: 0;
    transform: translateY(-8px);
    pointer-events: none;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.open {
    opacity: 1;
    transform: translateY(0);
    pointer-events: auto;
}

.downLoadImage {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 2px dashed #000;
    margin-top: 12px;
    padding: 24px;
    border-radius: 8px;
}

.downLoadImage:hover {
    border-color: #4a90e2;
}

.imagePreviewWrapper {
  position: relative;
  display: inline-block;
}

.imagePreview {
  width: 100%;
  height: auto;
  max-height: 120px;
  object-fit: cover;
  border-radius: 6px;
}

.deleteButton {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  cursor: pointer;
}
