.servicesSection {
  padding: 60px 15%;
  background-color: #fff9f5;
  text-align: center;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 40px;
  color: #222;
}

.highlight {
  color: #f14504;
}

.cardGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Luôn 4 card hàng ngang */
  gap: 30px;
}

.card {
  background: white;
  padding: 30px 20px;
  border-radius: 18px;
  box-shadow: 0 10px 26px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-bottom: 3px solid #f14504;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.08);
}

.card h3 {
  font-size: 1.2rem;
  color: #f14504;
  margin: 16px 0 10px;
}

.card p {
  color: #555;
  font-size: 0.95rem;
  line-height: 1.5;
  margin-bottom: 16px;
}

.card a {
  font-size: 0.9rem;
  font-weight: 500;
  color: #f14504;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s ease;
}

.card a:hover {
  border-color: #f14504;
}

@media (max-width: 1200px) {
  .cardGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 700px) {
  .cardGrid {
    grid-template-columns: 1fr;
  }

  .title {
    font-size: 2rem;
  }

  .card {
    padding: 24px 16px;
  }
}
